import { mapState } from 'vuex'
import { msg, errorMsg } from '../utils/common'
export default {
  name: 'cancel',
  data () {
    return {
      cancelText: ''
    }
  },
  computed: {
    ...mapState({
      courseId: state => state.course.courseId,
      courseName: state => state.course.courseName
    })
  },
  methods: {
    cancelCourse (item) {
      if (this.cancelText === '') {
        errorMsg(this, '原因不能为空', '120px')
        return
      }
      let url = ''
      let req = {}
      if (this.$store.state.userType === 'teacher') {
        url = '/tea/cancelCourse'
        req = {
          teacancelReason: '',
          courseId: this.courseId
        }
      } else {
        url = '/stu/cancelOrder'
        req = {
          cause: '',
          stuOpenid: 1000,
          courserId: this.courseId
        }
      }
      console.log(url, req)
      setTimeout(() => {
      // request({
      //   url: url,
      //   method: 'get',
      //   data: req
      // }).then(res => {
      //   console.log(res)
        const res = {
          code: 0,
          msg: 'success'
        }
        if (Number(res.code) === 0) {
          msg(this, '取消成功')
          this.$router.push({ path: this.$store.state.user.returnUrl })
        } else {
          errorMsg(this, res.msg, '160px')
        }
      }, 0)
    }
  }
}



